@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Montserrat:wght@400;600&family=Notable&display=swap);
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile {
  background: url(/career-portfolio/static/media/graduatepic.2872818c.png);
  background-size: cover;
  height: 250px;
  width: 250px; 
  background-position: center center;
  box-shadow: 0 20px 5px 5px rgba(0,0,0,0.2);
  animation: animateProfile 5s ease-in-out infinite;
  transition: all 0.3s ease-in-out;
}

@media only screen and (max-width: 640px) {
    .profile {
      transition: none;
      transition: all 0.3s ease-in-out;
      height: 150px;
      width: 150px;
    }
}

@keyframes animateProfile {
  0%{
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}
.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: url("/career-portfolio/static/media/wave green.2fc42a66.png");
  background-size: 1000px 100px;
  animation: animateWave 4s ease-in-out infinite;
  animation-delay: calc(var(--i) * 0.25s);
}

.wave#wave1 {
  z-index: 1000;
  opacity: 1;
  background-position-x: 400px;
}

.wave#wave2 {
  z-index: 999;
  opacity: 0.5;
  background-position-x: 300px;
}

.wave#wave3 {
  z-index: 998;
  opacity: 0.2;
  background-position-x: 200px;
}

.wave#wave4 {
  z-index: 997;
  opacity: 0.7;
  background-position-x: 100px;
}

@keyframes animateWave {
  0%, 100%
  {
    transform: translateY(0px);
  }
  50%
  {
    transform: translateY(10px);
  }
}

